import React, { memo, useContext, useState } from 'react';
import { variables } from '../contexts/variables';
import useGetMovieDetails from '../contexts/useGetMovieDetails';
import useGetMovieCast from '../contexts/useGetMovieCast';
import useGetMovieImages from '../contexts/useGetMovieImages';
import useGetMovieVideos from '../contexts/useGetMovieVideos';
import useGetSimilarMovies from '../contexts/useGetSimilarMovies';
import { CircularProgressbar } from 'react-circular-progressbar';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Tooltip from '@mui/material/Tooltip';
import MuiAlert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import ModalVideo from 'react-modal-video';
import MovieCast from './MovieCast';
import SimilarMovies from './SimilarMovies';

const Details = memo(() => {
  const [snackbarAddFav, setSnackbarAddFav] = useState(false);
  const [snackbarRemoveFav, setSnackbarRemoveFav] = useState(false);

  const {
    API_KEY,
    BASE_URL,
    setActiveMovie,
    activeMovie,
    favorites,
    setFavorites,
    open,
    setOpen,
    activeVideo,
    setActiveVideo,
    videoIsOpen,
    setVideoIsOpen,
    baseImageUrl,
    tinyBaseImageUrl,
    showSearchBar,
  } = useContext(variables);

  const movieUrl =
    BASE_URL + `/movie/${activeMovie}?` + API_KEY + `&language=en-US`;
  const castUrl =
    BASE_URL + `/movie/${activeMovie}/credits?` + API_KEY + `&language=en-US`;
  const videoUrl =
    BASE_URL + `/movie/${activeMovie}/videos?` + API_KEY + `&language=en-US`;
  const imagesUrl = BASE_URL + `/movie/${activeMovie}/images?` + API_KEY;
  const similarMoviesLink =
    BASE_URL +
    `/movie/${activeMovie}/recommendations?` +
    API_KEY +
    `&language=en-US&page=1`;

  const { details, movieLoading, movieErrors } = useGetMovieDetails(movieUrl);
  const percentage = Math.floor(details?.vote_average * 10);
  const tagline = details?.tagline;
  const overview = details?.overview;
  // eslint-disable-next-line
  const { cast, castLoading, castErrors } = useGetMovieCast(castUrl);
  const directorIndex = item => item.job === 'Director';
  const directorValidation = cast?.crew.findIndex(directorIndex);
  const { images, imagesErrors, imagesLoading } = useGetMovieImages(imagesUrl);
  const img = () => {
    if (images?.backdrops.length === 0) {
      return images?.posters[0].file_path;
    } else {
      return images?.backdrops[0].file_path;
    }
  };
  const poster = () => {
    const posterIndex = item => {
      return item.iso_639_1 === 'en' || item.iso_639_1 === 'es';
    };
    const posterValidation = images?.posters.findIndex(posterIndex);
    if (posterValidation === -1) {
      return images?.posters[0].file_path;
    } else {
      return images?.posters[posterValidation].file_path;
    }
  };
  const { videos, videosErrors, videosLoading } = useGetMovieVideos(videoUrl);
  const { similarMovies, similarMoviesError, similarMoviesLoading } =
    useGetSimilarMovies(similarMoviesLink);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    } else {
      setOpen(false);
      setSnackbarAddFav(false);
      setSnackbarRemoveFav(false);
    }
  };

  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

  const loadingSpinner = (
    <div className="loader-container">
      <div className="loader"></div>
    </div>
  );

  if (
    movieLoading ||
    castLoading ||
    imagesLoading ||
    videosLoading ||
    similarMoviesLoading
  ) {
    return loadingSpinner;
  }

  if (movieErrors) {
    console.log(movieErrors);
  }
  if (imagesErrors) {
    console.log(imagesErrors);
  }
  if (castErrors) {
    console.log(castErrors);
  }

  if (videosErrors) {
    console.log(videosErrors);
  }
  if (videosErrors) {
    console.log(videosErrors);
  }
  if (similarMoviesError) {
    console.log(similarMoviesError);
  }
  const addFavorite = () => {
    setFavorites(current => [...current, activeMovie]);
    setSnackbarAddFav(true);
  };
  const removeFavorite = activeMovie => {
    const filtered = favorites.filter(items => {
      return items !== activeMovie;
    });
    setFavorites(filtered);
    setSnackbarRemoveFav(true);
  };

  const detailsPlayTrailer = () => {
    if (videos?.results.length === 0) {
      setOpen(true);
    } else {
      const videoList = videos?.results;
      const hasOfficialTrailer = item => item.name === 'Official Trailer';
      const validation = videoList.findIndex(hasOfficialTrailer);
      if (validation === -1) {
        setActiveVideo(videos?.results[0].key);
        setVideoIsOpen(true);
      } else {
        setActiveVideo(videos?.results[validation].key);
        setVideoIsOpen(true);
      }
    }
  };

  return (
    <>
      <>
        {' '}
        {/* snackbar Section = display the alert when no trailer videos are found */}
        <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert onClose={handleClose} severity="info" sx={{ width: '100%' }}>
            No trailers were found for this Movie
          </Alert>
        </Snackbar>
      </>
      <>
        <Snackbar
          open={snackbarAddFav}
          autoHideDuration={3000}
          onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: '100%' }}>
            Movie added to favorites!
          </Alert>
        </Snackbar>
      </>
      <>
        <Snackbar
          open={snackbarRemoveFav}
          autoHideDuration={3000}
          onClose={handleClose}>
          <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
            Movie removed from favorites.
          </Alert>
        </Snackbar>
      </>
      <>
        {activeVideo && (
          <ModalVideo
            channel="youtube"
            isOpen={videoIsOpen}
            videoId={activeVideo}
            onClose={() => setVideoIsOpen(false)}
            showinfo="0"
          />
        )}
      </>
      {!showSearchBar && (
        <div className="return-container">
          <ArrowBackIcon
            className="return-icon"
            onClick={() => setActiveMovie('')}></ArrowBackIcon>
        </div>
      )}

      {images && (
        <div
          className="detail-image-container"
          style={{
            backgroundImage: `url(${baseImageUrl + img()})`,
          }}
          alt=""></div>
      )}
      <div className="detail-info-container">
        {images && (
          <img
            alt=""
            className="details-poster"
            src={baseImageUrl + poster()}></img>
        )}
        <div className="detail-information">
          <div className="details-titleandfav">
            {details && (
              <>
                <div className="title-year">
                  {details?.title} {` (${details?.release_date.slice(0, 4)})`}
                </div>
              </>
            )}
          </div>

          <div className="fav-container">
            <div className="fav-buttons">
              {favorites.includes(activeMovie) ? (
                <Tooltip title="Remove from favorites">
                  <button
                    className="detail-fav-button"
                    onClick={() => removeFavorite(activeMovie)}>
                    <FavoriteIcon className="mui-fav-icons" />
                  </button>
                </Tooltip>
              ) : (
                <Tooltip title="Add to favorites">
                  <button className="detail-fav-button" onClick={addFavorite}>
                    {' '}
                    <FavoriteBorderIcon
                      className="mui-fav-icons"
                      size="medium"
                    />{' '}
                  </button>
                </Tooltip>
              )}
            </div>
            <div className="fav-legend">
              {favorites.includes(activeMovie) ? (
                <h3>Remove From Favorites</h3>
              ) : (
                <h3>Add to Favorites</h3>
              )}
            </div>
          </div>

          <div className="progress-bar-container">
            <div className="progress-bar">
              <CircularProgressbar
                value={percentage}
                text={`${percentage}%`}
                styles={percentage >= 69 /*nice*/ ? goodMovie : badMovie}
              />
              <h2> User Score</h2>
            </div>
            <div className="detail-genres">
              <p>
                <span className="bigger-text">Genre:</span>{' '}
                {details?.genres.map(item => item.name + `. `)}
              </p>
              <p>
                <span className="bigger-text">Release date:</span>{' '}
                {details?.release_date + `.`}
              </p>
              <p>
                <span className="bigger-text">Duration:</span>
                {details?.runtime + ` minutes.`}
              </p>
            </div>
          </div>
          <div className="play-trailer-button" onClick={detailsPlayTrailer}>
            <button data-text="Awesome" className="button">
              <span className="actual-text">&nbsp;PlayTrailer&nbsp;</span>
              <span className="hover-text" aria-hidden="true">
                &nbsp;PlayTrailer&nbsp;
              </span>
            </button>
            {/* eslint-disable-next-line */}
            <marquee className="marquee">{tagline}</marquee>
            <div className="crew-container">
              <div className="crew-director">
                <h3>
                  <span className="bigger-text"> Director:</span>
                </h3>

                <p>{cast?.crew[directorValidation].name}</p>
              </div>
              <div className="crew-director">
                <h3>
                  <span className="bigger-text"> Producer Company:</span>
                </h3>

                {details?.production_companies.length > 0 && (
                  <p>{details?.production_companies[0].name}</p>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="tagline-overview-container">
        <h2>
          <span className="bigger-text"> Overview:</span>
        </h2>
        <p> {overview}</p>
      </div>

      {cast?.cast.length !== 0 && (
        <MovieCast cast={cast} tinyBaseImageUrl={tinyBaseImageUrl} />
      )}

      <SimilarMovies data={similarMovies} setActiveMovie={setActiveMovie} />
    </>
  );
});

//this part is for the % chart css//

const goodMovie = {
  root: { width: '70px' },
  path: {
    stroke: 'chartreuse' /*aqui cambia el color del llenado de la barra*/,
    transformOrigin: 'center center',
    strokeLinecap: 'round',
  },

  trail: {
    stroke: 'grey',
    opacity: '0.2',
  },
  text: {
    fill: 'white',
    dominantBaseline: 'middle',
    textAnchor: 'middle',
    // Text size
    fontSize: '42px',
  },
};

const badMovie = {
  root: { width: '70px' },
  path: {
    stroke: 'tomato' /*aqui cambia el color del llenado de la barra*/,
    strokeLinecap: 'round',
    transformOrigin: 'center center',
  },

  trail: {
    stroke: 'grey',
    opacity: '0.2',
  },
  text: {
    fill: 'white',
    dominantBaseline: 'middle',
    textAnchor: 'middle',
    // Text size
    fontSize: '42px',
  },
};

export default Details;
