import React, { memo, useEffect } from 'react';
import Slider from 'react-slick';

const similarMovies = memo(({ data, setActiveMovie }) => {
  const moviesArray = data?.results;

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  var settings = {
    accessibility: true,
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 2,
    swipeToSlide: true,
    initialSlide: 0,
    centerPadding: '60px',
    responsive: [
      {
        breakpoint: 1124,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 320,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: false,
          dots: true,
        },
      },
    ],
  };
  return (
    <>
      <h1 className="similarmovies-title">
        If you liked this movie, you may also like:
      </h1>
      <div className="trending-slider">
        <Slider {...settings}>
          {data &&
            moviesArray.slice(0, 19).map(item => {
              return (
                item.poster_path && (
                  <div
                    className="slider-container"
                    key={item.id}
                    onClick={() => setActiveMovie(item.id)}>
                    <img
                      className="trending-slider-img"
                      alt=""
                      src={
                        `https://image.tmdb.org/t/p/w200` + item.poster_path
                      }></img>
                    <p>{item.title}</p>
                  </div>
                )
              );
            })}
        </Slider>
      </div>
    </>
  );
});

export default similarMovies;
