import { useState, useEffect } from 'react';
import useFetch from './useFetch';

const useGetMovieImages = imagesUrl => {
  const [images, setImages] = useState(null);
  const [imagesErrors, setImagesErrors] = useState('');
  const [imagesLoading, setImagesLoading] = useState('');

  const { data, error, loading } = useFetch(imagesUrl);

  useEffect(() => {
    setImagesLoading(loading);
    setImagesErrors(error);
    setImages(data);
  }, [data, error, loading]);

  return { images, imagesErrors, imagesLoading };
};

export default useGetMovieImages;
