import React, { memo } from 'react';
import CastCard from './CastCard';

const MovieCast = memo(({ cast, tinyBaseImageUrl }) => {
  const acting = cast?.cast;

  return (
    <>
      <p className="movie-cast-title">Main Cast</p>
      {cast && (
        <div className="movie-cast-main">
          {acting
            .slice(0, 9)
            .map(
              item =>
                item.profile_path && (
                  <CastCard
                    key={item.id}
                    name={item.name}
                    character={item.character}
                    photo={item.profile_path}
                    tinyBaseImageUrl={tinyBaseImageUrl}
                  />
                )
            )}
        </div>
      )}
    </>
  );
});

export default MovieCast;
