import React, { useContext, useState, memo, useEffect, useRef } from 'react';
import { variables } from '../contexts/variables';
import CloseIcon from '@mui/icons-material/Close';
import { DebounceInput } from 'react-debounce-input';

const SearchBar = memo(() => {
  const [searchField, setSearchField] = useState('');
  const [resultsArray, setResultsArray] = useState([]);
  const [loading, setLoading] = useState(false);
  const { API_KEY, BASE_URL, setActiveMovie, setShowSearchBar } =
    useContext(variables);
  const focusSearch = useRef(null);
  const movieSearchUrl =
    searchField.length === 0
      ? BASE_URL + '/trending/movie/day?' + API_KEY
      : BASE_URL +
        `/search/movie?` +
        API_KEY +
        `&language=en-US&query=` +
        searchField +
        `&page=1&include_adult=false`;

  useEffect(() => {
    setLoading(true);
    fetch(`${movieSearchUrl}`, { method: 'get' })
      .then(response => response.json())
      .catch(error => console.log(error))
      .then(data => {
        const moviesArray = data.results.slice(0, 10).map(item => item);
        setResultsArray(moviesArray);
      });
    setLoading(false);
  }, [movieSearchUrl]);

  if (loading) {
    return (
      <div className="loader-container">
        <div className="loader"></div>
      </div>
    );
  }

  const goToDetails = id => {
    setActiveMovie(id);
    setShowSearchBar(false);
  };

  const clearSearch = () => {
    setResultsArray([]);
    setSearchField('');
    focusSearch.current.focus();
  };

  return (
    <>
      <div className="search-container">
        <DebounceInput
          minLength={2}
          debounceTimeout={1000}
          autoFocus
          inputRef={focusSearch} // ref cambia a inputRef por la libreria debounce
          className="search-input"
          type="text"
          placeholder="search movie"
          value={searchField}
          onChange={e => setSearchField(e.target.value)}
        />
        <CloseIcon
          onClick={clearSearch}
          className={
            searchField === ''
              ? 'search-clear-button'
              : 'search-clear-button-red'
          }
        />
      </div>
      {searchField !== '' && resultsArray.length === 0 && (
        <div className="no-results-container">
          <p>
            No movies called{' '}
            <span className="no-results-movie"> "{searchField}" </span> were
            found
          </p>
        </div>
      )}

      {resultsArray.length !== 0 && (
        <>
          {searchField === '' && (
            <div className="default-search">
              <p>Trending Movies:</p>
            </div>
          )}

          <div className="results-container">
            {resultsArray.map(
              item =>
                item.poster_path && (
                  <div
                    className="search-result"
                    key={item.id}
                    onClick={() => goToDetails(item.id)}>
                    <img
                      alt=""
                      className="search-item-img"
                      src={
                        `https://image.tmdb.org/t/p/w200` + item.poster_path
                      }></img>
                    <p>{item.title}</p>
                  </div>
                )
            )}
          </div>
        </>
      )}
    </>
  );
});

export default SearchBar;
