import React, { memo, useEffect } from 'react';
import Slider from 'react-slick';
import AddBoxIcon from '@mui/icons-material/AddBox';

const FavoriteSlider = memo(({ setActiveMovie, favorites }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  var settings = {
    accessibility: true,
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 2,
    swipeToSlide: true,
    initialSlide: 1,
    centerPadding: '60px',
    responsive: [
      {
        breakpoint: 1124,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 2,
          infinite: false,
          dots: true,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 2,
          infinite: false,
          dots: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: false,
          dots: true,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 320,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: false,
          dots: true,
        },
      },
    ],
  };
  return (
    <>
      <h1 className="favorites-title">What to watch</h1>
      <h3 className="favorites-subtitle">From your Favorites</h3>

      {favorites && favorites.length === 0 ? (
        <div className="empty-favorites">
          <AddBoxIcon className="addBoxIcon" />
          <h3 className="empty-favorites-text">
            Add movies to favorites to keep track of what you want to watch.
          </h3>
        </div>
      ) : (
        <div className="trending-slider">
          <Slider {...settings}>
            {favorites &&
              favorites.map(item => {
                return (
                  item.poster_path && (
                    <div
                      className="slider-container"
                      key={item.id}
                      onClick={() => setActiveMovie(item.id)}>
                      <img
                        className="trending-slider-img"
                        alt=""
                        src={
                          `https://image.tmdb.org/t/p/w200` + item.poster_path
                        }></img>
                      <p>{item.title}</p>
                    </div>
                  )
                );
              })}
          </Slider>
        </div>
      )}
    </>
  );
});

export default FavoriteSlider;
