import { useState, useEffect } from 'react';
import useFetch from './useFetch';

const useGetMovieCast = castUrl => {
  const [cast, setCast] = useState(null);
  const [castErrors, setCastErrors] = useState('');
  const [castLoading, setCastLoading] = useState(null);

  const { data, error, loading } = useFetch(castUrl);

  useEffect(() => {
    setCastLoading(loading);
    setCastErrors(error);
    setCast(data);
  }, [data, error, loading]);

  return { cast, castErrors, castLoading };
};

export default useGetMovieCast;
