import './styles/styles.css';
import './styles/detailsPlayButton.css';
import './styles/modal-video.css';
import './styles/movieCast.css';
import './styles/similarMovies.css';
import './styles/movieSlider.css';
import './styles/slick.css';
import './styles/slickTheme.css';
import './styles/responsiveness.css';

import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
// import Landing from './components/Landing';
import Header from './components/Header';
import { variables } from './contexts/variables';
import Main from './components/Main';
import { useScrollPosition } from './components/useScrollNavBar';
import Details from './components/Details';

const App = () => {
  const API_KEY = 'api_key=b48fb92f5118ac0cdfa61875710f18fe';
  const BASE_URL = 'https://api.themoviedb.org/3';
  const scrollPosition = useScrollPosition();
  const [welcomePage, setWelcomePage] = useState(true);
  const [activeMovie, setActiveMovie] = useState('');
  const [favorites, setFavorites] = useState([]);
  const [open, setOpen] = useState(false);
  const [videoIsOpen, setVideoIsOpen] = useState(false); //this state manages the ModalVideo (current video)
  const [activeVideo, setActiveVideo] = useState(null); //this state manages the Snackbar for no trailers availables
  const [showSearchBar, setShowSearchBar] = useState(false);
  //local storage to save favorite movies in the =State
  const baseImageUrl = `https://image.tmdb.org/t/p/w500`;
  const tinyBaseImageUrl = `https://image.tmdb.org/t/p/w200`;

  useEffect(() => {
    const temp = localStorage.getItem('data');
    const loadedData = JSON.parse(temp);

    if (loadedData) {
      setFavorites(loadedData);
    }
  }, []);

  useEffect(() => {
    const temp = JSON.stringify(favorites);
    localStorage.setItem('data', temp);
  }, [favorites]);

  return (
    <variables.Provider
      value={{
        welcomePage,
        setWelcomePage,
        API_KEY,
        BASE_URL,
        scrollPosition,
        activeMovie,
        setActiveMovie,
        favorites,
        setFavorites,
        open,
        setOpen,
        videoIsOpen,
        setVideoIsOpen,
        activeVideo,
        setActiveVideo,
        baseImageUrl,
        tinyBaseImageUrl,
        showSearchBar,
        setShowSearchBar,
      }}>
      <Router>
        <Header />
        <Routes>
          <Route
            path="/"
            element={activeMovie === '' ? <Main /> : <Details />}
          />
        </Routes>
      </Router>
    </variables.Provider>
  );
};

export default App;
