import { useState, useEffect } from 'react';
import useFetch from './useFetch';

const useGetMovieDetails = movieUrl => {
  const [details, setDetails] = useState(null);
  const [movieErrors, setMovieErrors] = useState('');
  const [movieLoading, setMovieLoading] = useState('');

  const { data, error, loading } = useFetch(movieUrl);

  useEffect(() => {
    setDetails(data);
    setMovieLoading(loading);
    setMovieErrors(error);
  }, [data, error, loading]);

  return { details, movieErrors, movieLoading };
};

export default useGetMovieDetails;
