import React, { memo, useContext, useEffect, useState } from 'react';
import { variables } from '../contexts/variables';
import InfoIcon from '@mui/icons-material/Info';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import StarIcon from '@mui/icons-material/Star';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import ModalVideo from 'react-modal-video';
import MovieSlider from './MovieSlider';
import FavoriteSlider from './FavoriteSlider';

const Main = memo(() => {
  const {
    API_KEY,
    BASE_URL,
    setActiveMovie,
    open,
    setOpen,
    activeVideo,
    setActiveVideo,
    videoIsOpen,
    setVideoIsOpen,
    favorites,
  } = useContext(variables);
  // URL de las peliculas populares
  const url = BASE_URL + '/discover/movie?sort_by=popularity.desc&' + API_KEY;
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [image, setImage] = useState('');
  const [title, setTitle] = useState('');
  const [overview, setOverview] = useState('');
  const [genres, setGenres] = useState([]);
  const [rating, setRating] = useState('');
  const [duration, setDuration] = useState('');
  const [localActiveMovie, setLocalActiveMovie] = useState(null);
  const [favoriteDetails, setFavoriteDetails] = useState(null);

  const videoUrl =
    BASE_URL +
    `/movie/${localActiveMovie}/videos?` +
    API_KEY +
    `&language=en-US`;

  // este use effect hace 2 llamadas a la api
  //1ra: lista de peliculas populares(para la imagen principal),
  // 2da informacion (movie ID) de esa pelicula para los detalles
  //--------------------------------------------------------------FETCH BLOCK--------------------//

  useEffect(() => {
    setLoading(true);

    const randomNum = Math.floor(Math.random() * 20);
    const popularMovies = fetch(`${url}`, { method: 'get' })
      .then(response => response.json())
      .catch(err => {
        console.error('Request failed', err);
      })
      // recibe los datos de la primera promesa
      .then(data => {
        const movieId = data.results[randomNum].id;
        setData(data);
        setLocalActiveMovie(movieId);
        setImage(
          `https://image.tmdb.org/t/p/original/${data.results[randomNum].backdrop_path}`
        );
        setTitle(data.results[randomNum].title);
        setOverview(data.results[randomNum].overview);
        setRating(data.results[randomNum].vote_average);

        return fetch(
          BASE_URL + `/movie/${movieId}?` + API_KEY + `&language=en-US`,
          { method: 'get' }
        );
      })
      .then(response => response.json())
      .catch(err => {
        console.error('Request failed', err);
      });
    popularMovies.then(res => {
      // 2nd request

      setGenres(res.genres);
      setDuration(res.runtime);
      setLoading(false);
    });
    const getFavoriteDetails = async () => {
      setLoading(true);
      const favDetails = await Promise.all(
        favorites.map(item =>
          fetch(BASE_URL + `/movie/${item}?` + API_KEY + `&language=en-US`)
            .then(res => res.json())
            .catch(error => {
              console.log(error);
            })
        )
      );
      setLoading(false);
      return setFavoriteDetails(favDetails);
    };
    setFavoriteDetails(getFavoriteDetails());
  }, [API_KEY, BASE_URL, url, favorites]);

  //--------------------------------------------------------------FETCH BLOCK--------------------//

  if (loading) {
    return (
      <div className="loader-container">
        <div className="loader"></div>
      </div>
    );
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    } else {
      setOpen(false);
    }
  };

  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

  const moreInfoButton = () => {
    setActiveMovie(localActiveMovie);
  };
  const playTrailer = () => {
    setLoading(true);
    fetch(videoUrl)
      .then(response => response.json())
      .then(data => {
        if (data.results.length === 0) {
          setOpen(true);
        } else {
          const videoList = data.results;
          const hasOfficialTrailer = item => item.name === 'Official Trailer';
          const validation = videoList.findIndex(hasOfficialTrailer);
          if (validation === -1) {
            setActiveVideo(videoList[0].key);
            setVideoIsOpen(true);
          } else {
            setActiveVideo(videoList[validation].key);
            setVideoIsOpen(true);
          }
        }
      });
    setLoading(false);
  };

  return (
    <>
      <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="inowfo" sx={{ width: '100%' }}>
          No trailers were found for this Movie
        </Alert>
      </Snackbar>
      <>
        {activeVideo && (
          <ModalVideo
            channel="youtube"
            isOpen={videoIsOpen}
            videoId={activeVideo}
            onClose={() => setVideoIsOpen(false)}
            showinfo="0"
          />
        )}
      </>
      <div
        className="info-container"
        style={{ backgroundImage: `url(${image})` }}>
        <div className="main-info">
          <h1 className="main-title">{title}</h1>
          <div className="main-genres">
            <div className="genres-info-one">
              {genres.map(item => (
                <p key={item.id} className="p">
                  {item.name}
                </p>
              ))}
            </div>
            <div className="genres-info-two">
              <StarIcon className="main-rating" />
              <div className="rating"> {rating}/10</div>
              <p className="duration">{`- Duration: ${duration} Minutes`}</p>
            </div>
          </div>
          <p className="main-overview">{overview}</p>
          <div className="button-container">
            <button className="main-button" onClick={playTrailer}>
              {' '}
              <PlayCircleIcon /> Watch Trailer
            </button>
            <button className="main-button" onClick={moreInfoButton}>
              {' '}
              <InfoIcon /> More Info
            </button>
          </div>
        </div>
      </div>
      <MovieSlider data={data} setActiveMovie={setActiveMovie} />
      <FavoriteSlider
        data={data}
        setActiveMovie={setActiveMovie}
        favorites={favoriteDetails}
      />
    </>
  );
});

export default Main;
