import { useState, useEffect } from 'react';
import useFetch from './useFetch';

const useGetMovieVideos = castUrl => {
  const [videos, setVideos] = useState(null);
  const [videosErrors, setVideosErrors] = useState('');
  const [videosLoading, setVideosLoading] = useState('');

  const { data, error, loading } = useFetch(castUrl);

  useEffect(() => {
    setVideosLoading(loading);
    setVideosErrors(error);
    setVideos(data);
  }, [data, error, loading]);

  return { videos, videosErrors, videosLoading };
};

export default useGetMovieVideos;
