import React, { useContext, useState } from 'react';
import { variables } from '../contexts/variables';
import SearchBar from './SearchBar';
import SearchIcon from '@mui/icons-material/Search';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import { debounce } from 'debounce';

const Header = () => {
  const [currentWidth, setCurrentWidth] = useState(0);
  const { scrollPosition, setActiveMovie, showSearchBar, setShowSearchBar } =
    useContext(variables);

  const updateCurrentWidth = () => {
    setCurrentWidth(window.innerWidth);
  };

  window.onresize = debounce(updateCurrentWidth, 150);

  return (
    <>
      <div className={scrollPosition < 20 ? 'nav-header' : 'nav-header-below'}>
        <img
          alt="logo"
          onClick={() => setActiveMovie('')}
          className={currentWidth > 600 ? 'header-logo' : 'header-logo-square'}
          src={
            process.env.PUBLIC_URL + currentWidth > 600
              ? '/second-logo.svg'
              : '/square-logo.svg'
          }></img>
        <ul className="nav-list"></ul>

        <div className="search-icon-container">
          {!showSearchBar ? (
            <>
              <SearchIcon
                className="search-button"
                onClick={() => setShowSearchBar(!showSearchBar)}
              />
            </>
          ) : (
            <HighlightOffIcon
              className="search-button-close"
              onClick={() => setShowSearchBar(!showSearchBar)}
            />
          )}
        </div>
      </div>
      {showSearchBar && <SearchBar setShowSearchBar={setShowSearchBar} />}
    </>
  );
};

export default Header;
