import { useState, useEffect } from 'react';
import useFetch from './useFetch';

const useGetSimilarMovies = castUrl => {
  const [similarMovies, setSimilarMovies] = useState(null);
  const [similarMoviesError, setSimilarMoviesError] = useState('');
  const [similarMoviesLoading, setSimilarMoviesLoading] = useState('');

  const { data, error, loading } = useFetch(castUrl);

  useEffect(() => {
    setSimilarMoviesLoading(loading);
    setSimilarMoviesError(error);
    setSimilarMovies(data);
  }, [data, error, loading]);

  return { similarMovies, similarMoviesError, similarMoviesLoading };
};

export default useGetSimilarMovies;
