import React, { memo } from 'react';

const CastCard = memo(({ name, character, tinyBaseImageUrl, photo }) => {
  return (
    <div className="card-container">
      <img
        className="card-photo"
        alt="artist"
        src={tinyBaseImageUrl + photo}
        onError={e => {
          if (
            e.target.src !==
            process.env.PUBLIC_URL + `/images/blankprofile.png`
          ) {
            e.target.src = process.env.PUBLIC_URL + `/images/blankprofile.png`;
          }
        }}></img>
      <h3 className="card-name">{name}</h3>
      <p className="card-character">{character}</p>
    </div>
  );
});

export default CastCard;
